//Colorchange.vue

<template>
  <div>
    <textarea id="textarea" rows=10 cols=80 v-model="result"></textarea>
    <button id="button" v-on:click="toggleStartStop()">Click to speak</button>
  </div>
</template>

<script type="text/javascript">
export default {
  name: 'voice',
  data () {
    return {
      button: document.getElementById('#button'),
      textarea: document.querySelector('textarea'),
      recognizing: null,
      recognition: new webkitSpeechRecognition(),
      result: null
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    reset: function () {
      this.recognizing = false
    },
    init: function () {
      this.recognition.continuous = true
      this.reset()
      this.recognition.onend = this.reset()
      this.recognition.onresult = (event) => {
        for (var i = event.resultIndex; i < event.results.length; ++i) {
          if (event.results[i].isFinal) {
            this.result = event.results[i][0].transcript
            textarea.value = this.result
            console.log("return result: ", this.result) 
          }
        }
      }
    },
    toggleStartStop: function () {
      if (this.recognizing) {
        this.recognition.stop()
        this.reset()
      } else {
        this.recognition.start()
        this.recognizing = true
      }
    }
  }
}
</script>



